.register {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}
.reg-form {
  padding: 5%;
  display: flex;
  max-height: 700px;
  min-width: 25%;
  align-items: center;
  justify-content: center;
  gap: 50px;
  flex-direction: column;
  border-radius: 10px;
  background-color: #f1ff2d;
}
.reg-form__input {
  height: 50px;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  border-radius: 10px;
  padding: 10px;
  width: 100%;
}

.reg-form {
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 20px;
}
.reg-form button {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  padding: 15px;
  border-radius: 10px;
  width: 100%;
}
.reg-form h3 {
  font-size: 30px;
}
.register img {
  position: relative;
  height: 20%;
}
.register img.--1 {
  bottom: 30%;
  transform: rotateZ(20deg);
}

.register img.--2 {
  top: 20%;
}
.register table {
  display: flex;
  width: 100%;
  gap: 30px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.register table tr {
  width: 90%;
}
@media (max-width: 900px) {
  .reg-form__input {
    width: 300px;
  }
  .register img {
    display: none;
  }
}
